header {
  position: fixed;
  width: 100%;
  background-color: #000000;
  z-index: 10001; 
}

.header-inner {
  height: 0;
  padding-top: 42px;
  position: relative; 
  color: #FFFFFF;
  font-size: 2rem;
  font-family: 'Circular', 'Noto Sans', 'Calibri', 'Arial', 'Helvetica','Corbel','Garamond';
  -webkit-box-sizing: border-box; 
  font-weight: bold; 
}

@media (min-width: 768px) {
  .header-inner {
    padding-top: 78px; } }

.logo {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  display: block; }
  .logo img {
    display: block;
    width: 138px;
    height: 24px;
    color: #ffffff; }

@media (min-width: 768px) {
  .logo {
    left: 10%;
    -webkit-transform: translate(0%, -50%);
    -ms-transform: translate(0%, -50%);
    transform: translate(0%, -50%); }
    .logo img {
      display: block;
      width: 184px;
      height: 32px; } }

footer {
  width: 100%;
  display: block;
  position: fixed;
  bottom: 0%;
  background-color: #222222;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#222222), to(#333333));
  background-image: -webkit-linear-gradient(top, #222222 0%, #333333 100%);
  background-image: -o-linear-gradient(top, #222222 0%, #333333 100%);
  background-image: linear-gradient(top, #222222 0%, #333333 100%);
  z-index: 10000; }

@media (min-width: 768px) {
  footer {
    background-color: #e9e9e9; } }

.pc__copyright {
  display: none;
  height: 30px;
  line-height: 30px;
  font-size: 0.75rem;
  max-width: 1080px;
  margin: 0 auto;
  position: relative;
  color: #404040;
  padding: 0 1rem; }

@media (min-width: 768px) {
  .pc__copyright {
    display: block; } }

.m__copyright {
  display: block;
  height: 36px;
  line-height: 36px;
  width: 100%;
  position: relative;
  color: #ffffff; }

@media (min-width: 768px) {
  .m__copyright {
    display: none; } }

.footer__copyright {
  display: inline-block;
  color: #1ED760;
  margin: 0; }

.footer__terms {
  display: inline-block;
  list-style-type: none;
  margin: 0 auto;
  padding: 0;
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  white-space: nowrap;
  letter-spacing: 0.02rem; }
  .footer__terms li {
    display: inline; }
    .footer__terms li a {
      color: #1ED760;
      font-size: 0.75rem; }

.footer__terms li:after {
  content: " | ";
  color: #707070; }

.footer__terms li:nth-last-child(1):after {
  content: ""; }

@media (min-width: 768px) {
  .footer__terms {
    left: auto;
    right: 0;
    -webkit-transform: translateX(-1rem);
    -ms-transform: translateX(-1rem);
    transform: translateX(-1rem); } }

